import {ElMessageBox} from "element-plus";

export async function confirmDialog(message, title = '提示', type = 'warning') {
    try {
        const confirmResult = await ElMessageBox.confirm(message, title, {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: type,
        });
        return confirmResult === 'confirm'; // 返回 true 表示确认，false 表示取消
    } catch (error) {
        return false; // 捕获取消操作，返回 false
    }
}

export function copyToClipboard(text) {
    navigator.clipboard.writeText(text)
        .then(() => {
            this.$message.success('复制成功！内容：' + text);
        })
        .catch(() => {
            // 兼容性处理
            const textarea = document.createElement('textarea');
            textarea.value = text;
            document.body.appendChild(textarea);
            textarea.select();
            document.execCommand('copy');
            document.body.removeChild(textarea);
            this.$message.success('复制成功！内容：' + text);
        });
}